}

main {
  min-height: 100vh;
  display: grid;
  place-items: center;
}

/*
=============== 
Nav
===============
*/
nav {
  background: #fff;
  background: var(--clr-white);
  height: 3rem;
  display: grid;
  align-items: center;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  box-shadow: var(--dark-shadow);
}
.app_nav_center__Pto6u {
  width: 90vw;
  max-width: 620px;
  max-width: var(--fixed-width);
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.app_nav_center__Pto6u img {
  height: 2.5rem;
  margin-right: 1.5rem;
  border-radius: 2rem;
}
.app_nav_center__Pto6u h4 {
  margin-bottom: 0;
  color: hsl(205, 78%, 60%);
  color: var(--clr-primary-5);
}
.app_nav_links__16_s5 {
  display: flex;
}
nav a {
  text-transform: capitalize;
  font-weight: 700;
  font-size: 1rem;
  color: hsl(205, 86%, 17%);
  color: var(--clr-primary-1);
  letter-spacing: 0.1rem;
  letter-spacing: var(--spacing);
  margin-right: 1rem;
}
nav a:hover {
  color: hsl(205, 78%, 60%);
  color: var(--clr-primary-5);
}
@media screen and (max-width: 500px) {
  nav {
    height: 3rem;
  }
  .app_nav_center__Pto6u {
    width: 80vw;
  }

  .app_nav_links__16_s5 {
    display: flex;
  }
  nav a {
    font-weight: 800;
    font-size: 0.7rem;
    margin: 0.2rem;
  }
}
/*
=============== 
Container
===============
*/
